  RepoSyncResponse,
  RepoSyncAllResponse,
  RepoCommitStatusResponse,
  RepoCommitInfo,
  AuthConfig,
  UserResponse,
  WorkspacesResponse,
  LinkedRepoResponse,
  TokenResponse,
  RefreshResponse,
  ContentTreeResponse,
  CleanupDeadReposResponse,
  // Database types
  DatabaseCreate,
  DatabaseUpdate,
  DatabaseConfigResponse,
  DatabaseListResponse,
  DatabaseSchemaResponse,
  DatabaseSampleResponse,
  DatabaseDiagramResponse,
  // Prototype types
  PrototypeChatListResponse,
  PrototypeChatPreviewResponse,
  PrototypeChatLinkRequest,
  PrototypeChatLinkResponse,
  PrototypeChatSyncRequest,
