  /**
   * Set GitHub token for the authenticated user
   * Stores the token server-side linked to the Cognito identity
   * @param token - GitHub Personal Access Token
   * @returns Success response
   */
  async setGitHubToken(token: string): Promise<{ success: boolean }> {
    const headers = await this.getHeaders();
    const response = await this.client.request<{ success: boolean } | true, unknown>({
      method: 'PUT',
      url: '/auth/github-token',
      headers,
      body: { github_token: token },
    });
    if (response.error) {
      throw new Error(`Failed to set GitHub token: ${JSON.stringify(response.error)}`);
    }
    // Handle case where response.data might be true (boolean) or object
    if (response.data === true) {
      return { success: true };
    }
    return response.data || { success: true };
  }

  /**
   * Get GitHub token status for the authenticated user
   * @returns Token status (configured or not)
   */
  async getGitHubTokenStatus(): Promise<{ configured: boolean; username?: string }> {
    const headers = await this.getHeaders();
    const response = await this.client.request<{ configured: boolean; username?: string }, unknown>(
      {
        method: 'GET',
        url: '/auth/github-token',
        headers,
      }
    );
    if (response.error) {
      throw new Error(`Failed to get GitHub token status: ${JSON.stringify(response.error)}`);
    }
    const data = response.data;
    if (data && typeof data === 'object' && 'configured' in data) {
      return data as { configured: boolean; username?: string };
    }
    return { configured: false };
  }

  /**
   * Delete GitHub token for the authenticated user
   * @returns Success response
   */
  async deleteGitHubToken(): Promise<{ success: boolean }> {
    const headers = await this.getHeaders();
    const response = await this.client.request<{ success: boolean } | true, unknown>({
      method: 'DELETE',
      url: '/auth/github-token',
      headers,
    });
    if (response.error) {
      throw new Error(`Failed to delete GitHub token: ${JSON.stringify(response.error)}`);
    }
    // Handle case where response.data might be true (boolean) or object
    if (response.data === true) {
      return { success: true };
    }
    return response.data || { success: true };
  }

  // ============== Project Credential Operations ==============

  /**
   * Get project credentials for frontend authentication
   * @param projectId - Project ID
   * @returns Project credentials
   */
  async getProjectCredentials(projectId: string): Promise<ProjectCredentials> {
    const headers = await this.getHeaders();
    const response = await sdk.getCredentials({
      client: this.client,
      path: { project_id: projectId },
      headers,
    });
    if (response.error) {
      throw new Error(`Failed to get credentials: ${JSON.stringify(response.error)}`);
    }
    const data = response.data as CredentialGetResponse;
    if (!data.data) {
      throw new Error('No credentials found');
    }
    return data.data as ProjectCredentials;
  }

  /**
   * Save project credentials for frontend authentication
   * @param projectId - Project ID
   * @param credentials - Credentials to save (url, username, password)
   * @returns Success response
   */
  async saveProjectCredentials(
    projectId: string,
    credentials: { url: string; username: string; password: string; login_instructions?: string }
  ): Promise<CredentialActionResponse> {
    const headers = await this.getHeaders();
    const body: CredentialCreate = {
      url: credentials.url,
      username: credentials.username,
      password: credentials.password,
      login_instructions: credentials.login_instructions || '',
      workspace: this.workspace,
    };
    const response = await sdk.saveCredentials({
      client: this.client,
      path: { project_id: projectId },
      headers,
      body,
    });
    if (response.error) {
      throw new Error(`Failed to save credentials: ${JSON.stringify(response.error)}`);
    }
    return response.data as CredentialActionResponse;
  }

  /**
   * Update project credentials for frontend authentication
   * @param projectId - Project ID
   * @param updates - Partial credential updates
   * @returns Success response
   */
  async updateProjectCredentials(
    projectId: string,
    updates: Partial<ProjectCredentials>
  ): Promise<CredentialActionResponse> {
    const headers = await this.getHeaders();
    const body: CredentialUpdate = {
      url: updates.url ?? undefined,
      username: updates.username ?? undefined,
      password: updates.password ?? undefined,
      login_instructions: updates.login_instructions ?? undefined,
      workspace: updates.workspace ?? undefined,
    };
    const response = await sdk.updateCredentials({
      client: this.client,
      path: { project_id: projectId },
      headers,
      body,
    });
    if (response.error) {
      throw new Error(`Failed to update credentials: ${JSON.stringify(response.error)}`);
    }
    return response.data as CredentialActionResponse;
  }

