/** Response from generateOutput */
export interface GenerateOutputResponse {
  session_id: string;
  status: string;
  output_type: string;
  doc_types?: string[];
  message?: string;
}

/** Response from getOutputStatus */
export interface OutputStatusResponse {
  status: 'pending' | 'generating' | 'ready' | 'failed';
  output_type: string;
  generated_docs: string[];
  progress?: number;
  message?: string;
  error?: string;
}

/**
 * Mission namespace for convenient mission operations
 */
export class MissionNamespace {
  constructor(private client: OCXPClient) {}

  /**
   * List missions with optional filtering and pagination
   * @example ocxp.mission.list({ status: 'active', limit: 10 })
   * @example ocxp.mission.list({ limit: 20, offset: 40, orderBy: 'created_at', orderDir: 'desc' })
   */
  async list(
    options?: ListOptions & {
      projectId?: string;
      status?: string;
      missionIds?: string[];
      includeMetadata?: boolean;
    }
  ): Promise<MissionListResponse> {
    return this.client.listMissions(options);
  }

  /**
   * Get a mission by ID
   * @example ocxp.mission.get('uuid')
   */
  async get(missionId: string): Promise<MissionResponse> {
    return this.client.getMission(missionId);
  }

  /**
   * Create a new mission with auto-generated UUID
   * @example ocxp.mission.create({ title: 'My Mission', description: 'Description' })
   */
  async create(data: {
    title: string;
    description?: string;
    projectId?: string;
    goals?: string[];
  }): Promise<MissionResponse> {
    return this.client.createMission(data.title, data.description, data.projectId, data.goals);
  }

  /**
   * Update mission
   */
  async update(
    missionId: string,
    updates: {
      title?: string;
      description?: string;
      status?: string;
      progress?: number;
      notes?: string;
    }
  ): Promise<MissionResponse> {
    return this.client.updateMission(missionId, updates);
  }

  /**
   * Delete mission
   */
  async delete(missionId: string): Promise<void> {
    return this.client.deleteMission(missionId);
  }

  /**
   * Add session to mission
   */
  async addSession(missionId: string, sessionId: string): Promise<MissionResponse> {
    return this.client.addMissionSession(missionId, sessionId);
  }

  /**
   * Remove session from mission
   */
  async removeSession(missionId: string, sessionId: string): Promise<MissionResponse> {
    return this.client.removeMissionSession(missionId, sessionId);
  }

  /**
   * Regenerate mission - archives old docs and triggers AgentCore
   * @example ocxp.mission.regenerate('uuid', { ticket_id: 'AMC-123' })
   */
  async regenerate(
    missionId: string,
    options?: {
      ticket_id?: string;
      ticket_summary?: string;
      ticket_description?: string;
      archive_old_docs?: boolean;
      auto_increment_version?: boolean;
    }
  ): Promise<RegenerateMissionResponse> {
    return this.client.regenerateMission(missionId, options);
  }

  /**
   * Download mission pack as ZIP
   * @example await ocxp.mission.download('mission-id')
   */
  async download(missionId: string): Promise<Blob> {
    return this.client.downloadMissionPack(missionId);
  }

  /**
   * Get mission context for agents
   * @example ocxp.mission.getContext('uuid')
   */
  async getContext(missionId: string) {
    return this.client.getMissionContext(missionId);
  }

  /**
   * Get mission content tree structure from S3
   * @param includeVersions - If true, includes S3 version IDs for files
   * @example ocxp.mission.tree('mission-id', 5, true)
   */
  async tree(
    path?: string,
    depth?: number,
    includeVersions?: boolean
  ): Promise<ContentTreeResponse> {
    return this.client.tree('mission', path, depth, includeVersions);
  }

  /**
   * Generate mission output (documents, reports, etc.)
   * General endpoint for all output types using existing research.
   *
   * @param missionId - Mission UUID
   * @param outputType - Type of output: 'documents', 'report', 'summary', etc.
   * @param options - Output options (doc_types, strategy, etc.)
   * @returns Output response with session_id for tracking
   *
   * @example
   * await ocxp.mission.generateOutput('mission-id', 'documents', {
   *   doc_types: ['implementation-guide', 'prd'],
   *   strategy: 'generate_all'
   * })
   */
  async generateOutput(
    missionId: string,
    outputType: string,
    options?: {
      doc_types?: string[];
      strategy?: string;
      session_id?: string;
      options?: Record<string, unknown>;
    }
  ): Promise<GenerateOutputResponse> {
    return this.client.generateMissionOutput(missionId, outputType, options);
  }

  /**
   * Get output generation status
   * Check progress and status of output generation.
   *
   * @param missionId - Mission UUID
   * @param outputType - Type of output to check (default: 'documents')
   * @returns Current output status with progress
   *
   * @example
   * const status = await ocxp.mission.getOutputStatus('mission-id', 'documents')
   */
  async getOutputStatus(
    missionId: string,
    outputType: string = 'documents'
  ): Promise<OutputStatusResponse> {
    return this.client.getMissionOutputStatus(missionId, outputType);
  }
}

